import React from 'react';
import {NavLink} from 'react-router-dom'

function Menu() {
    return (
        <div className="menu">
            <NavLink exact activeClassName="active-nav" to='/react-router'> About </NavLink>
            <NavLink exact activeClassName="active-nav" to='/react-router/api'> API </NavLink>
            <NavLink exact activeClassName="active-nav" to='/react-router/contact'> Contact </NavLink>
        </div>
    )
}

export default Menu
